ngerhover{border:1px solid #ff4c4c}.danger.pressed{border:1px solid #ff4c4c;box-sizing:border-box;border-radius:8px;box-shadow:#ffa5a5 0px 0px 0px 2px}.danger.pressed label{color:#ff4c4c}.danger input{caret-color:#ff4c4c}.danger .select-icon{color:#ff4c4c;background:#ffa5a5}.danger.select-wrapper{border:1px solid #ff4c4c}.disabledhover{border:1px solid #e7edf4}.disabled.pressed{border:1px solid #e7edf4}.disabled input{caret-color:#ff4c4c;cursor:not-allowed}.disabled .select-icon{color:#b9cbd3;background:#f8fbfb}.disabled.select-wrapper{border:1px solid #e7edf4;background:#f3f6fa;cursor:not-allowed}.options-wrapper{width:100%;max-height:9rem;position:absolute;left:0;z-index:1000;margin-top:4px;border-radius:8px;background:#f8fbfb;box-shadow:0px 2px 8px rgba(96,123,153,0.3);overflow-y:auto;transition:transform 0.25s, opacity 0.75s, visibility 0.75s, -webkit-transform 0.25s;transform-origin:top left;transform:scaleY(0);visibility:hidden;opacity:0}.options-wrapper.open{visibility:visible;transform:scale(1);opacity:1}.option-container{width:100%;display:flex;align-items:center;background:#ffffff;padding:8px;padding-left:12px;cursor:pointer;box-sizing:border-box;outline:none;user-select:none}.option-container:hover{background:#f3f6fa}.option-container .option{display:flex;align-items:center;justify-content:space-between;color:#202c44;font-size:0.875rem;line-height:150%;font-family:'Nunito Sans', Tahoma, Helvetica, Arial, sans-serif;font-style:normal;font-weight:normal;margin:0px;padding:0px;border:0px}.chip-container{display:flex;margin:2px;min-width:0px;box-sizing:border-box;border-radius:8px;background:#e8f2ff;color:#3f7de8}.chip-container .chip-label{font-family:'Nunito Sans', Tahoma, Helvetica, Arial, sans-serif;font-weight:600;font-size:0.75rem;overflow:hidden;padding:3px 3px 3px 6px;text-overflow:ellipsis;white-space:nowrap;box-sizing:border-box}.chip-container .chip-delete{display:flex;padding-left:4px;padding-right:4px;box-sizing:border-box;align-items:center;cursor:pointer}::-webkit-scrollbar{width:14px;background-color:transparent}::-webkit-scrollbar-thumb{border:4px solid transparent;border-radius:10px;background-clip:content-box;background-color:#b9cbd3}
